let PivotLayoutMixin = {
  props: {
    parent: {
      type: Object,
      required: true
    }
  },
  data: function () {
    return {
      gridApi: null,
      gridOptions: null,
      sideBar: null,
      statusBar: null
    }
  },
  methods: {
    exportCsv () {
      let self = this
      this.gridApi.exportDataAsCsv({
        fileName: this.$t('PAGE_TITLE'),
        processCellCallback: function (params) {
          if (params.column.getColDef().filter && params.column.getColDef().filter === 'agDateColumnFilter') {
            return self.formatDate(params)
          } else {
            return params.value
          }
        }
      })
    },
    exportXls () {
      let self = this
      this.gridApi.exportDataAsExcel({
        fileName: this.$t('PAGE_TITLE'),
        sheetName: 'Export',
        processCellCallback: function (params) {
          if (params.column.getColDef().filter && params.column.getColDef().filter === 'agDateColumnFilter') {
            return self.formatDate(params)
          } else {
            return params.value
          }
        }
      })
    },
    formatNumber (params) {
      if (params.node.group) {
        return params.value
      } else {
        let number = parseFloat(params.value).toFixed(2)
        return Number(number).toLocaleString('en')
      }
    },
    formatDate (params) {
      return this.$options.filters.formatUnixDate(params.value)
    }
  },
  beforeMount () {
    this.gridOptions = {
      rowSelection: 'single',
      colResizeDefault: 'shift',
      rowGroupPanelShow: 'always',
      defaultColDef: {
        resizable: true,
        sortable: true,
        filter: true
      }
    }

    this.sideBar = {
      toolPanels: [{
        id: 'columns',
        labelDefault: 'Columns',
        labelKey: 'columns',
        iconKey: 'columns',
        toolPanel: 'agColumnsToolPanel'
      }, {
        id: 'filters',
        labelDefault: 'Filters',
        labelKey: 'filters',
        iconKey: 'filter',
        toolPanel: 'agFiltersToolPanel'
      }],
      defaultToolPanel: 'columns'
    }

    this.statusBar = {
      statusPanels: [{
        statusPanel: 'agTotalRowCountComponent',
        align: 'left'
      }, {
        statusPanel: 'agFilteredRowCountComponent'
      }, {
        statusPanel: 'agSelectedRowCountComponent'
      }, {
        statusPanel: 'agAggregationComponent'
      }]
    }
  },
  mounted: function () {
    this.gridApi = this.gridOptions.api
  }
}

export default PivotLayoutMixin
